.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  /* width: 4.875rem; */
  width: 7.625rem;
  height: 100%;
  /* background-color: #111614; */
  background: linear-gradient(180deg, #6cbfb2 0%, #1b9c86 100%);
  color: #f1f1f1;
  padding-bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  transition: all 0.2s;
  overflow-y: hidden;
  z-index: 9;
}

/* .sidebar:hover, */
.sidebar.sidebar-open {
