}

.main {
  width: calc(100% - 7.625rem);
  min-height: 100vh;
  color: var(--text-color);
  transition: all 0.2s;
  box-sizing: border-box;
  background: #fafafa;
}
.sidebar.sidebar-open + .main {
  width: calc(100% - 20rem);
}

.sidebar.sidebar-open + .main .dashboard-navbar {
  width: calc(100% - 20rem);
}

.sidebar.sidebar-close + .main .dashboard-navbar {
