.close_icon {
  background-color: #000;
  border-radius: 5px;
  color: #fff;
  padding-left: 5px;
  padding-right: 5px;
  min-width: 45px;
  position: relative;
  cursor: pointer;
}
.close_icon::after {
  content: "";
  position: absolute;
  width: 3px;
  height: 20px;
  border-radius: 8px;
  background: #fff;
  left: 22px;
  top: 12px;
  transform: rotate(45deg);
}
.close_icon::before {
  content: "";
  position: absolute;
  width: 3px;
  height: 20px;
  border-radius: 8px;
  background: #fff;
  left: 22px;
  top: 12px;
  transform: rotate(-45deg);
}

